body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  min-height: 100vh;
  max-height: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.react-container {
  width: 95%;
}

.input-task-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 500px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.task-input {
  width: 90%;
  border: none;
  border-bottom: 2px solid #6e4d0c;
  padding-top: 1rem;
  padding-left: 0.6rem;
}

.task-input--error {
  border: 3px solid #ed0000;
}

.btn.new-task-btn {
  border: 2px solid #b36f1f;
  background-color: #f1c734;
  border-radius: 50%;
  font-size: 1.5rem;
  font-weight: bold;
  width: 2rem;
  height: 2rem;
  padding: 0;
  margin: 1rem 0;
}

.btn.new-task-btn:hover {
  border-color: #8f520b;
  background-color: #be9506;
}

body {
  background-color: #ebdab0;
}

.wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  width: 100%;
  color: #fff;
  font-size: 1.3em;
}

.box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border: 2px solid #86673e;
  background-color: #ffebcd;
  margin: 0.5rem 0;
  padding: 10px;
  width: 300px;
  height: auto;
}

.text {
  width: 100%;
  text-align: justify;
  margin: 0;
  color: #2b1000;
  font-size: 1.3rem;
}

.box:last-child {
  margin-bottom: 0;
}

.buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-top: 1rem;
}

.btn {
  margin-right: 10px;
  padding: 5px;
  margin-top: 10px;
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: bold;
  color: #fff;
  outline: none;
}

.edit-btn {
  background-color: #e0be43;
  border: 2px solid #b6821a;
}

.edit-btn:hover {
  background-color: #b6821a;
  border-color: #946912;
}

.remove-btn {
  background-color: #da755a;
  border: 2px solid #b04b30;
}

.remove-btn:hover {
  background-color: #bb563a;
  border-color: #952c10;
}

.save-btn {
  background-color: #e9b20c;
  border: 2px solid #f69b11;
}

.save-btn:hover {
  background-color: #c79910;
  border-color: #ab6d10;
}

textarea {
  display: block;
  width: 100%;
  box-sizing: border-box;
  border: 2px solid #6c54bf;
  resize: none;
}

